import React from "react";
import ScreenCalculator from "./ScreenCalculator";
import Keyboard from "./Keyboard";

export default function MainCalculator() {
  return (
    <div
      className="display-column-center"
      style={{
        minWidth: "100vw",
        minHeight: "100vh",
      }}
    >
      <ScreenCalculator />
      <Keyboard />
    </div>
  );
}
